import React, { useState, useEffect } from "react";
import Axios from "axios";
import BootstrapTable from "react-bootstrap-table-next";
import NewsCard from "./NewsCard";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
export default function NewsPage() {
  const [newsSource, setNewsSource] = useState([]);
  const [dailyRead, setDailyRead] = useState([]);
  const [readTabs, setReadTabs] = useState([]);
  const readThings =  ['hacker_news','huxiu','36kr','MIT','zaobao','wsj','nfzm'];
  const [c] = useState(0);
  const dest_url = ("https://cors-anywhere.herokuapp.com/http://"+process.env.REACT_APP_IP??"127.0.0.1")+":"+(process.env.REACT_APP_PORT??"538");

  useEffect(() => {
    Axios.get(
      `${dest_url}/source?file=news`
    ).then((data) => {
      setNewsSource(
        data.data.data.map((item, index) => {
          return item.name;
        })
      );
    });
  }, [c]);
  useEffect(() => {
    const get_list =readThings.map((item) => {
        return Axios.get(
          `${dest_url}/rss?name=${item}`
        );
      });
      Promise.all(get_list)
      .then((values) => {
          setReadTabs(
          values.map((item,index)=>{
            var tmp_rs = item.data.data.map((titem, index) => {
                return {name:titem.name,url:titem.url};
              });
            return {name:readThings[index],rs:tmp_rs};
          }));}
      );
  }, [c]);
  useEffect(() => {
    const get_list = newsSource.map((item) => {
      return Axios.get(
        `${dest_url}/daily_read?entity=${item}&daily_read_date=3`
      );
    });
    var index = 0;
    // load data
    Promise.all(get_list)
      .then((values) => {
        var result = [];
        values.map((rs) => {
          const read_item = rs.data.data;

          read_item.map((item) => {
            result.push({
              id: index,
              name: item.entity,
              date: item.date,
              url: item.url,
              name: item.name,
            });
            index += 1;
          });
        });

        setDailyRead(result);
      })
  }, [newsSource]);
  const columns = [
    {
      dataField: "id",
      text: "ID",
    },
    {
      dataField: "name",
      text: "Name",
    formatter:(cell,row)=>{return <a href={row.url} target="_blank" rel="noopener noreferrer">{row.name}</a>}
    },
    {
      dataField: "date",
      text: "DATE",
    }
  ];
  return <>
  <Container>
  <Row>
      {readTabs.map((tab) => {return <Col key={tab.name}><NewsCard data={tab.rs} name={tab.name}/>
  </Col>;})}
    </Row>
  <Row>
    <Col><BootstrapTable keyField="id" data={dailyRead.sort((a,b)=>{return b.date.localeCompare(a.date)})} columns={columns} /></Col>
    
  </Row>
</Container>
  
  </>;
}
